import { gql } from 'apollo-boost';

export const USERS = gql`
    query users($limit: Int, $skip: Int){
        users(limit: $limit, skip: $skip) {
            id,
            username,            
            siteCount,
            totalSitesCost,
            totalReceivedAmount,
            spent,
            balance,
            createdAt,
            updatedAt,
            count,
        }
    }
`;

export const USER = gql`
    query user($id: String!){
        user(id: $id) {
            id,
            username,
            siteCount,
            totalSitesCost,
            totalReceivedAmount,
            spent,
            balance,
            createdAt,
            updatedAt            
        }
    }
`;

export const USER_CREDIT_HISTORY = gql`
    query userCreditHistory($id: String!, $limit: Int, $skip: Int){
        userCreditHistory(id: $id, limit: $limit, skip: $skip) {
            count
            history {
                amount
                createdAt
            }
        }
    }
`;

export const USERS_USERNAME = gql`
    query users($limit: Int, $skip: Int){
        users(limit: $limit, skip: $skip) {
            id
            username
        }
    }
`;

export const SITES = gql`
  query sites($limit: Int, $skip: Int) {
    sites(limit: $limit, skip: $skip) {
        id,
        name,
        location,
        entryCount,
        createdAt,
        updatedAt,
        count
        manager {
            id,
            name: username
        }
        managerSpentAmount,
        cost
    }
  }
`;

