export const SITE = gql`
  query site($id: String!) {
    site(id: $id) {
        id,
        name,
        location,
        createdAt,
        updatedAt,
        count
        manager {
            id,
            name: username
        }        
    }
  }
`;

const repeater = `{
    quantity,
    cost,
    paid
}`;

export const SITE_ENTRIES = gql`
    query siteEntries($id: String!, $limit: Int!, $skip: Int){
        siteEntries(id: $id, limit: $limit, skip: $skip){
            id,
            name,
            location,
            cost,
            count,
            manager {
                id,
                name: username,                
            },
            managerSpentAmount,
            entries
            {
                id,
                createdAt,
                updatedAt,                
                mistri ${repeater},
                labour ${repeater},
                eit ${repeater},
                morang ${repeater},
                githi ${repeater},
                cement ${repeater},
                saria ${repeater},
                dust ${repeater},
                other ${repeater},
                managerSpentAmount,
                total
            }
        }
    }
`

export const SITE_ENTRY = gql`
    query siteEntry($id: String!){
        siteEntry(id: $id){
            id,
            createdAt,
            updatedAt,                
            mistri ${repeater},
            labour ${repeater},
            eit ${repeater},
            morang ${repeater},
            githi ${repeater},
            cement ${repeater},
            saria ${repeater},
            dust ${repeater},
            other ${repeater},
            managerSpentAmount,
            total
        }
    }
`

export const PROPERTIES = gql`
    query properties($limit: Int!, $skip: Int){
        properties(limit: $limit, skip: $skip){
            id
            name
            location
            price
            buyer
            buyerNumber
            totalReceivedAmount
            balance
            nextDueDate
            count
            createdAt
            updatedAt            
        }
    }
`;

export const PROPERTY = gql`
    query property($id: String!){
        property(id: $id){
            id
            name
            location
            price
            buyer
            totalReceivedAmount
            balance
            nextDueDate            
            createdAt
            updatedAt            
        }
    }
`;

export const PROPERTY_CREDIT_HISTORY = gql`
    query propertyCreditHistory($id: String!, $limit: Int, $skip: Int){
        propertyCreditHistory(id: $id, limit: $limit, skip: $skip) {
            count
            history {
                amount
                createdAt
            }
        }
    }
`;



